ul {
  list-style: none;
  padding: 0;
  margin: 0;
}


.burger-btn__close-icon {
    margin-right: 20px;
    margin-bottom: 20.26px;
}


.burger-btn {

    @media screen and (max-width: 767px) {
        font-family: DM Sans, sans-serif;
        display: flex;
        justify-content: space-between;
        align-items: center;
        text-align: center;
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 1.31;
        letter-spacing: .04em;
        transition: .25s cubic-bezier(.4, 0, .2, 1);
        color: #d31342;
        // width: 186px;
        height: 44px;
        margin: 60px 21px 19px 20px;
        padding-left: 48.64px;
        padding-right: 48.64px;
        background: #fff;
        border: transparent;
        border-radius: 22px;
    }


    
    @media screen and (min-width: 480px) and (max-width: 767px) {
        // margin: 60px 34px 19px;
        padding-left: 43.28px;
        padding-right: 43.28px;
    }

    @media screen and (min-width: 768px) {
        display: none;
    }

}

.burger-btn__arrow-icon {
    margin-left: 15.77px;
}


.mobile-menu {
  display: none;
}


.modal-toggle__close {
  position: absolute;
  right: 28px;
  top: 23px;
  padding: 0;
  border: none;
  background-color: inherit;
  cursor: pointer;

  @media (min-width: 768px) {
    right: 34px;
  }
}

.modal-toggle.is-open {
  transform: translateX(0);
}

.modal-toggle {
    
    @media screen and (max-width: 479px) {
        position: fixed;
        top: 0;
        right: 0;
        width: 100%;
        height: 100%;
        overflow-x: hidden;
        overflow-y: auto;
        padding-bottom: 50px;
        background-color: #e17992;
        z-index: 9999;
        transform: translateX(100%);
        transition: transform 0.25s cubic-bezier(0.4, 0, 0.2, 1);
    }

    @media screen and (min-width: 480px) and ( max-width: 767px) {
        position: fixed;
        top: 0;
        right: 0;
        width: 254px;
        height: 100%;
        overflow-x: hidden;
        overflow-y: auto;
        padding-bottom: 50px;
        background-color: #e17992;
        z-index: 9999;
        transform: translateX(100%);
        transition: transform 0.25s cubic-bezier(0.4, 0, 0.2, 1);
    }
  
}

.link {
  text-decoration: none;
  color: inherit;
}

.burger-link {
  margin: 24px;
  text-align: center;
  transition: 0.25s cubic-bezier(0.4, 0, 0.2, 1);
}

.burger__item {
  width: 250px;
  height: 42px;
  border-top: 1px solid #e18298;
}

.burger__item,
.burger__nav {
  display: flex;
  align-items: center;
}

.burger__nav {
    

    @media screen and (max-width: 767px) {
        font-family: DM Sans, sans-serif;
        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        line-height: 18px;
        letter-spacing: 0.04em;
        color: #fff;
        border-bottom: 1px solid #e18298;
    }

    @media screen and (min-width: 768px) {
        display: none;
    }
  
}



.overlay-block.open {
  display: block;
}

.overlay-block {
    @media screen and (max-width: 767px) {
        position: fixed;
        display: none;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;       
        background-color: rgba(0, 0, 0, 0.75);        
        z-index: 9998;
    }
  
}
